import React, { useEffect, useState } from 'react';
import Table, { TableColumnType } from '../../components/Table';
import { sortByKey } from '../../utils/sort';

type Type = 'string' | 'number';

interface TableViewProps {
  columns: TableColumnType[];
  dataSource: any[] | undefined;
  onSelectRow?: (item: any) => void;
  onSort?: (item: any) => void;
}

const TableView: React.FC<TableViewProps> = ({
  columns,
  dataSource,
  onSelectRow,
  onSort
}) => {
  const [sourceData, setSourceData] = useState<any[] | undefined>([]);
  const [isReverse, setIsReverse] = useState<boolean>(false);
  useEffect(() => {
    setSourceData(dataSource);
  }, [dataSource]);

  return (
    <>
      <Table
        columns={columns}
        dataSource={sourceData}
        rowKey={(data: string) => {
          return data;
        }}
        onHeaderClick={(e: string) => dataSource?.length && onSort?.(e)}
        onRowClick={(item: any) => onSelectRow?.(item)}
      />
    </>
  );
};

export default TableView;

