const DEVICE_KEY = 'trungnam_cms-device';
const TOKEN_KEY = 'trungnam_cms-token';
const USER_ID_KEY = 'trungnam_cms-user-id';
const USER = 'trungnam_cms-user';

export default class LocalStorageHelper {
  public static get device() {
    return localStorage.getItem(DEVICE_KEY) || undefined;
  }
  public static set device(value) {
    localStorage.setItem(DEVICE_KEY, value || '');
  }

  public static get token() {
    return localStorage.getItem(TOKEN_KEY) || undefined;
  }
  public static set token(value) {
    localStorage.setItem(TOKEN_KEY, value || '');
  }
  public static get userId() {
    return localStorage.getItem(USER_ID_KEY);
  }
  public static set userId(value) {
    localStorage.setItem(USER_ID_KEY, value || '');
  }
  public static get user() {
    return localStorage.getItem(USER);
  }
  public static set user(value) {
    localStorage.setItem(USER, value || '');
  }
}
