export function formatMoney(
  paramMoney: string | number,
  separate: string = '.'
) {
  if (!paramMoney) {
    return paramMoney;
  }
  const moneyValue = paramMoney.toString();
  const regex = /\B(?=(\d{3})+(?!\d))/g;
  return moneyValue.replace(regex, separate);
}

export function formatPageNumber(pageLength: number | undefined) {
  let result: number = 1;
  if (!pageLength) {
    return result;
  }

  if (pageLength === 10) {
    result = 1;
    return;
  }

  result =
    pageLength % 10 === 0 ? pageLength % 10 : Math.floor(pageLength / 10) + 1;

  return result;
}

export function formatDateTime(date: Date) {
  function fillZero(num: number) {
    return `0${num}`.substr(-2);
  }
  const y = date.getFullYear();
  const M = fillZero(date.getMonth() + 1);
  const d = fillZero(date.getDate());
  const h = fillZero(date.getHours());
  const m = fillZero(date.getMinutes());
  const s = fillZero(date.getSeconds());

  return `${y}/${M}/${d} ${h}:${m}:${s}`;
}
