import { Button, SvgIcon, TextField } from '@material-ui/core';
import { mdiPlus } from '@mdi/js';
import qs from 'query-string';
import React, { useEffect, useState } from 'react';
import {
  RouterProps,
  useHistory,
  useLocation,
  useRouteMatch
} from 'react-router';
import checklistApis from '../../../../api/checklist';
import Table from '../../../../components/Table';
import { useQuery } from '../../../../hook/use-query';
import {
  Checklist,
  getQuestionTypeName,
  Question,
  QuestionTypes
} from '../../../../models/Checklist';
import { formatDateTime } from '../../../../utils/format';
import { QuestionForm } from './QuestionForm';
import { Content, Root, Title } from './styled';

type P = {};

export const QuestionList: React.FC<P> = (p) => {
  const { params } = useRouteMatch<{
    id: string;
  }>();
  const query = useQuery<{
    question?: number;
    create: string | boolean | undefined;
  }>();
  const history = useHistory();
  const [checklist, setChecklist] = useState<Checklist>();
  const isCreate = query.create?.toString() === 'true';

  const question =
    checklist?.questions && query.question
      ? checklist.questions.find((e) => +query.question! === e.id)
      : undefined;

  useEffect(() => {
    checklistApis.getDetail(params.id).then((res) => {
      setChecklist(res);
    });
  }, [params.id]);

  function handleQuestionUpdated() {
    checklistApis.getDetail(params.id).then((res) => {
      setChecklist(res);
    });
  }

  return (
    <Root>
      <Content>
        <Title>
          Bộ câu hỏi: {checklist?.name}{' '}
          <Button
            color="primary"
            onClick={() => {
              history.push({
                pathname: history.location.pathname,
                search: qs.stringify({
                  question: undefined,
                  create: true
                })
              });
            }}>
            <SvgIcon>
              <path d={mdiPlus} />
            </SvgIcon>
            Thêm câu hỏi
          </Button>
        </Title>
        <Table
          stickyHeader={true}
          rowKey={(row: Question) => row.id}
          columns={[
            {
              title: 'ID',
              dataIndex: 'id',
              key: 'id'
            },
            {
              title: 'Nội dung',
              dataIndex: 'question',
              key: 'question'
            },
            {
              title: 'Loại câu hỏi',
              dataIndex: 'type',
              key: 'type',
              render: getQuestionTypeName
            },
            {
              title: 'Thông tin thêm',
              key: 'additional_info',
              dataIndex: 'additional_info',
              render: (data: string | null) => (data ? 'Có' : 'Không')
            },
            {
              key: 'created_at',
              title: 'Ngày tạo',
              dataIndex: 'created_at',
              render: (v: string) => {
                console.log(v);

                return formatDateTime(new Date(v));
              }
            },
            {
              key: 'updated_at',
              title: 'Cập nhật',
              dataIndex: 'updated_at',
              render: (v: string) => formatDateTime(new Date(v))
            }
          ]}
          onRowClick={(row: Question) => {
            history.push({
              pathname: history.location.pathname,
              search: qs.stringify({
                question: row.id,
                create: undefined
              })
            });
          }}
          dataSource={checklist?.questions}
        />
      </Content>
      {(!!question || isCreate) && (
        <QuestionForm
          isCreate={isCreate}
          question={question}
          checklistId={checklist?.id}
          onUpdated={handleQuestionUpdated}
        />
      )}
    </Root>
  );
};
