import { Button, SvgIcon, TextField } from '@material-ui/core';
import { mdiPlus } from '@mdi/js';
import qs from 'query-string';
import React, { useEffect, useState } from 'react';
import {
  RouterProps,
  useHistory,
  useLocation,
  useRouteMatch
} from 'react-router';
import { Link } from 'react-router-dom';
import Select from 'react-select';
import { toast } from 'react-toastify';
import checklistApis from '../../../../../api/checklist';
import ScrollView from '../../../../../components/ScrollView';
import { useQuery } from '../../../../../hook/use-query';
import {
  Checklist,
  getQuestionTypeName,
  Question,
  QuestionTypes
} from '../../../../../models/Checklist';
import codeGeneration from '../../../../../utils/code-generation';
import { FormItem, Input, RightPane, RightPaneBottom, Title } from './styled';

type P = {
  checklist?: Checklist;
  isCreate: boolean;
  onUpdated: (data: Checklist) => void;
};

export const DetailForm: React.FC<P> = (p) => {
  const [content, setContent] = useState(p.checklist?.name || '');
  const [isLoading, setIsLoading] = useState(false);
  const query = useQuery();
  const history = useHistory();

  useEffect(() => {
    if (!p.isCreate) {
      setContent(p.checklist?.name || '');
      return;
    }
    setContent('');
  }, [p.isCreate, p.checklist]);

  function create() {
    setIsLoading(true);
    checklistApis
      .create({
        name: content,
        vehicle_type: codeGeneration(content)
      })
      .then((v) => {
        history.push({
          pathname: `/checklist/${v.id}`
        });
      })
      .catch((e) => {
        toast.error(e.msg || e.message || 'Lỗi');
      })
      .finally(() => {
        setIsLoading(false);
      });
  }
  function update() {
    if (!p.checklist) {
      return;
    }
    setIsLoading(true);
    checklistApis
      .update(p.checklist.id, {
        name: content,
        vehicle_type: codeGeneration(content)
      })
      .then((v) => {
        toast.success('Cập nhật  thành công');
        p.onUpdated(v);
      })
      .catch((e) => {
        toast.error(e.msg || e.message || 'Lỗi');
      })
      .finally(() => {
        setIsLoading(false);
      });
  }

  return (
    <RightPane>
      <Title>Thông tin bộ câu hỏi</Title>
      <ScrollView>
        <FormItem>
          <span className="form-item-label">Loại thiết bị: </span>
          <Input
            value={content}
            placeholder="Loại thiết bị"
            onChange={(e) => setContent(e.currentTarget.value)}
          />
        </FormItem>
        {!p.isCreate && !!p.checklist && (
          <FormItem style={{ marginTop: 16 }}>
            <Link
              to={{
                pathname: `/checklist/${p.checklist.id}`
              }}>
              <Button variant="contained" color="primary">
                Xem danh sách câu hỏi
              </Button>
            </Link>
          </FormItem>
        )}
      </ScrollView>
      <RightPaneBottom>
        {p.isCreate ? (
          <Button
            disabled={isLoading}
            variant="contained"
            color="primary"
            onClick={create}>
            Thêm
          </Button>
        ) : (
          <Button
            disabled={isLoading}
            variant="contained"
            color="primary"
            onClick={update}>
            Cập nhật
          </Button>
        )}
      </RightPaneBottom>
    </RightPane>
  );
};
