import { Button, TextField } from '@material-ui/core';
import qs from 'query-string';
import React, { useEffect, useState } from 'react';
import {
  RouterProps,
  useHistory,
  useLocation,
  useRouteMatch
} from 'react-router';
import Select from 'react-select';
import { toast } from 'react-toastify';
import checklistApis from '../../../../../api/checklist';
import ScrollView from '../../../../../components/ScrollView';
import { useQuery } from '../../../../../hook/use-query';
import {
  Checklist,
  getQuestionTypeName,
  Question,
  QuestionTypes
} from '../../../../../models/Checklist';
import {
  Content,
  FormItem,
  Input,
  RightPane,
  RightPaneBottom,
  Root,
  Title
} from './styled';

type P = {
  question?: Question;
  checklistId?: string | number;
  isCreate: boolean;
  onUpdated: () => void;
};

const yesNoOptions = [
  { label: 'Không', value: 0 },
  { label: 'Có', value: 1 }
];

const questionOptions = Object.values(QuestionTypes).map((idx) => ({
  label: getQuestionTypeName(idx),
  value: idx
}));

export const QuestionForm: React.FC<P> = (p) => {
  const [order, setOrder] = useState(p.question?.order || 1);
  const [content, setContent] = useState(p.question?.question || '');
  const [type, setType] = useState<0 | 1 | 2>(
    p.question?.type || QuestionTypes.Status
  );
  const [isAdditional, setIsAdditional] = useState<0 | 1>(
    p.question?.additional_info ? 1 : 0
  );
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (!p.isCreate) {
      setOrder(p.question?.order || 1);
      setContent(p.question?.question || '');
      setType(p.question?.type || QuestionTypes.Status);
      setIsAdditional(p.question?.additional_info ? 1 : 0);
      return;
    }
    setOrder(1);
    setContent('');
    setType(QuestionTypes.Status);
    setIsAdditional(0);
  }, [p.question]);

  const orderValue = order.toString();
  const typeValue = { label: getQuestionTypeName(type), value: type };
  const additional = yesNoOptions[isAdditional];

  function create() {
    if (p.checklistId) {
      checklistApis
        .createQuestion(p.checklistId, [
          {
            order,
            question: content,
            type,
            additional_info: isAdditional ? 'true' : undefined
          }
        ])
        .then((v) => {
          toast.success('Cập nhật  thành công');
          p.onUpdated();
        })
        .catch((e) => {
          toast.error(e.msg || e.message || 'Lỗi');
        })
        .finally(() => {
          setIsLoading(false);
        });
    }
  }
  function update() {
    if (p.checklistId) {
      // checklistApis
      //   .createQuestion(p.checklistId, [
      //     {
      //       order,
      //       question: content,
      //       type,
      //       additional_info: isAdditional ? 'true' : undefined
      //     }
      //   ])
      //   .then((v) => {
      //     toast.success('Cập nhật  thành công');
      //     p.onUpdated();
      //   })
      //   .catch((e) => {
      //     toast.error(e.msg || e.message || 'Lỗi');
      //   })
      //   .finally(() => {
      //     setIsLoading(false);
      //   });
    }
  }

  return (
    <RightPane>
      <Title>Thông tin câu hỏi</Title>
      <ScrollView>
        <FormItem>
          <span className="form-item-label">Thứ tự: </span>
          <Input
            value={orderValue}
            placeholder="Thứ tự"
            type="number"
            min={1}
            onChange={(e) => setOrder(+(e.currentTarget.value || '1'))}
          />
        </FormItem>
        <FormItem>
          <span className="form-item-label">Nội dung: </span>
          <Input
            value={content}
            placeholder="Nội dung câu hỏi"
            onChange={(e) => setContent(e.currentTarget.value)}
          />
        </FormItem>
        <FormItem>
          <span className="form-item-label">Loại câu hỏi: </span>
          <Select
            onChange={(v) => setType(v?.value || 0)}
            isSearchable={false}
            value={typeValue}
            options={questionOptions}
            placeholder="Loại câu hỏi"
          />
        </FormItem>
        <FormItem>
          <span className="form-item-label">Thông tin thêm: </span>
          <Select
            isSearchable={false}
            onChange={(v) => setIsAdditional((v?.value as 0 | 1) || 0)}
            value={additional}
            options={[
              { label: 'Không', value: 0 },
              { label: 'Có', value: 1 }
            ]}
            placeholder="Thông tin thêm"
          />
        </FormItem>
      </ScrollView>
      <RightPaneBottom>
        {p.isCreate ? (
          <Button
            disabled={isLoading}
            variant="contained"
            color="primary"
            onClick={create}>
            Thêm
          </Button>
        ) : (
          <Button
            disabled={isLoading}
            variant="contained"
            color="primary"
            onClick={update}>
            Cập nhật
          </Button>
        )}
      </RightPaneBottom>
    </RightPane>
  );
};
