import { StyleButton } from './styled';
import React, { ReactElement } from 'react';

export interface ButtonProps {
  onClick?: () => void;
  title?: string | number;
  disable?: boolean;
  icon?: ReactElement;
  colorIcon?: string;
  color?: string;
  border?: string | undefined;
  background?: string;
  width?: string;
  status?: ReactElement;
  type?: 'button' | 'submit' | 'reset' | undefined;
}

const Button: React.FC<ButtonProps> = ({
  onClick,
  title,
  disable = false,
  icon,
  colorIcon,
  color = '#fff',
  border,
  background,
  width,
  status,
  type
}) => {
  return (
    <StyleButton
      type={type}
      disabled={disable}
      onClick={onClick}
      title=""
      color={color}
      colorIcon={colorIcon}
      border={border}
      background={background}
      width={width}
      className="styleButton">
      {title}
      <div className="styleIcon">
        {status}
        {icon}
      </div>
    </StyleButton>
  );
};

export default Button;
