import React, { ReactChild } from 'react';
import Button from '../Button';
import LabelInput from '../LabelView';
import ScrollView from '../ScrollView';
import {
  BtnConfirm,
  BtnReject,
  Content,
  FooterBtn,
  StyleContentRight,
  StyleTop,
  WrapContent
} from './styled';

interface ContentProps {
  childrenTop: ReactChild | undefined;
  childrenContent: ReactChild | undefined;
  onReject?: () => void | any | undefined;
  titleReject?: string | number;
  onConfirm?: () => void;
  titleConfirm?: string | number;
  disableBtnConfirm?: boolean;
  disableBtnReject?: boolean;
}

const ContentRight: React.FC<ContentProps> = ({
  childrenTop,
  childrenContent,
  onReject,
  titleReject,
  onConfirm,
  titleConfirm,
  disableBtnConfirm,
  disableBtnReject
}) => {
  return (
    <StyleContentRight>
      <StyleTop>{childrenTop}</StyleTop>
      <Content>
        <ScrollView>
          <WrapContent>{childrenContent}</WrapContent>
        </ScrollView>
      </Content>
      {titleConfirm && (
        <FooterBtn>
          {typeof onReject === 'function' && (
            <BtnReject>
              <Button
                onClick={() => {
                  onReject();
                }}
                title={titleReject}
                background="var(--color-danger)"
                disable={disableBtnReject}
              />
            </BtnReject>
          )}
          {typeof onConfirm === 'function' && (
            <BtnConfirm>
              <Button
                onClick={() => onConfirm?.()}
                title={titleConfirm}
                background="var(--color-primary)"
                disable={disableBtnConfirm}
              />
            </BtnConfirm>
          )}
        </FooterBtn>
      )}
    </StyleContentRight>
  );
};

export default ContentRight;
