function codeGeneration(str: string) {
  str.toLowerCase();
  let AccentsMap = [
    'aàảãáạăằẳẵắặâầẩẫấậ',
    'dđ',
    'eèẻẽéẹêềểễếệ',
    'iìỉĩíị',
    'oòỏõóọôồổỗốộơờởỡớợ',
    'uùủũúụưừửữứự',
    'yỳỷỹýỵ'
  ];
  for (let i = 0; i < AccentsMap.length; i++) {
    const re = new RegExp('[' + AccentsMap[i].substr(1) + ']', 'g');
    const char = AccentsMap[i][0];
    if (str) {
      str = str.replace(re, char).replace(/\s/g, '-');
    }
  }
  return str;
}

export default codeGeneration;
