import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import moment from 'moment';
import React, { useEffect, useState } from 'react';
import { toast } from 'react-toastify';
import styled from 'styled-components';
import ContentRight from '../../../components/ContentRight';
import LabelView from '../../../components/LabelView';
import Loading from '../../../layout/Loading';
import { formatMoney } from '../../../utils/format';
import { optionHomepage, OptionType } from '../../../utils/option';
import {
  renderOptionSelect,
  returnValueDefaultSelect
} from '../../../utils/render-data-select';
import Input from '../../../components/Input';
import Notification from '../../../layout/Notification';
//
import SelectBox from '../../../components/SelectBox';
import {
  addCompany,
  deleteCompany,
  getCompanyDetail,
  getProvinceList,
  updateCompany
} from '../../../api';

type Status = 'Active' | 'Inactive';

interface MenuInfoProps {
  idCode: string;
  onLoad: () => void;
}

let valueChangeInput: any = {};
const MenuInfo: React.FC<MenuInfoProps> = ({ idCode, onLoad }) => {
  const [valueDetail, setValueDetail] = useState<any>();
  const [provinceList, setProvinceList] = useState<any>();
  const [isDisable, setIsDisable] = useState<boolean>(true);
  const [isOpenNoti, setIsOpenNoti] = useState<boolean>(false);

  useEffect(() => {
    setIsDisable(true);
    setValueDetail('');
    if (!provinceList) {
      getProvince();
    }
    if (idCode.indexOf('New') != -1) {
      return;
    }
    getDetailCompany(idCode);
  }, [idCode]);

  function getDetailCompany(idCompany: string) {
    getCompanyDetail(idCompany)
      .then((res: any) => {
        setValueDetail(res);
      })
      .catch((err) => {
        toast.error(err?.message || 'Xảy ra lỗi bất ngờ');
      });
  }

  function getProvince() {
    getProvinceList()
      .then((res: any) => {
        setProvinceList(res);
      })
      .catch((err) => {
        toast.error(err?.msg || 'Xảy ra lỗi bất ngờ');
      });
  }

  function handleAddCompany() {
    if (
      !valueChangeInput.name ||
      !valueChangeInput.eng_name ||
      !valueChangeInput.province_id
    ) {
      toast.warning('Vui lòng nhập đủ thông tin!');
      return;
    }
    addCompany(valueChangeInput)
      .then((res: any) => {
        toast.success('Thêm Thành Công!');
        setValueDetail(res);
        setIsDisable(true);
        onLoad();
      })
      .catch((err) => {
        toast.error(err?.msg || 'Xảy ra lỗi bất ngờ');
      });
  }

  function handleUpdateCompany() {
    if (
      valueChangeInput.name === '' ||
      valueChangeInput.eng_name === '' ||
      valueChangeInput.province_id === ''
    ) {
      toast.warning('Vui lòng nhập đủ thông tin!');
      return;
    }
    const valueUpdate = {
      name: valueChangeInput.name,
      eng_name: valueChangeInput.eng_name,
      province_id: valueChangeInput.province_id
    };
    updateCompany(valueDetail?.id, valueUpdate)
      .then((res: any) => {
        toast.success('Update Thành Công!');
        setIsDisable(true);
        onLoad();
      })
      .catch((err) => {
        toast.error(err?.msg || 'Xảy ra lỗi bất ngờ');
      });
  }

  function handleDeleteCompany() {
    deleteCompany(valueDetail?.id)
      .then((res: any) => {
        toast.success('Delete Thành Công!');
        setValueDetail('');
        onLoad();
      })
      .catch((err) => {
        toast.error(err?.msg || 'Xảy ra lỗi bất ngờ');
      });
  }

  return (
    <ContentRight
      childrenTop={
        <>
          <div className="idCode">
            Code#
            <div className="code">{valueDetail?.id || 'New Company'}</div>
          </div>
          {valueDetail?.updated_at && (
            <span className="timeUpdate">
              Update at&nbsp;
              <span className="time">
                {valueDetail?.updated_at || valueDetail?.create_at}
              </span>
            </span>
          )}
          {/* { <Loading />} */}
          <Notification
            visible={isOpenNoti}
            title={`Xác nhận xóa: ${valueDetail?.name}`}
            onclose={() => {
              setIsOpenNoti(false);
            }}
            onConfirm={() => {
              setIsOpenNoti(false);
              handleDeleteCompany();
            }}
          />
        </>
      }
      childrenContent={
        <>
          <>
            <Input
              title="Tên tiếng việt"
              value={valueDetail?.name || ''}
              valueInput={(e: string | null) => {
                isDisable && setIsDisable(false);
                valueChangeInput.name = e;
              }}
            />

            <Input
              title="Tên tiếng anh"
              value={valueDetail?.eng_name || ''}
              valueInput={(e: string | null) => {
                isDisable && setIsDisable(false);
                valueChangeInput.eng_name = e;
              }}
            />

            <DivSelect>
              <span className="status">Province</span>
              <SelectBox
                optionDefault={renderOptionSelect(provinceList)}
                onChangeOption={(e: OptionType) => {
                  valueChangeInput = {
                    ...valueChangeInput,
                    province_id: e?.value
                  };
                  isDisable && setIsDisable(false);
                }}
                valueDefault={returnValueDefaultSelect(
                  renderOptionSelect(provinceList),
                  Number.parseInt(valueDetail?.province_id)
                )}
              />
            </DivSelect>
          </>
        </>
      }
      titleConfirm={!valueDetail?.name ? 'Add' : 'Update'}
      titleReject="Delete"
      disableBtnConfirm={isDisable}
      onConfirm={() => {
        !valueDetail?.name ? handleAddCompany() : handleUpdateCompany();
      }}
      onReject={
        !valueDetail?.name
          ? undefined
          : () => {
              setIsOpenNoti(true);
            }
      }
    />
  );
};

export default MenuInfo;

