import Enum from '../utils/enum';
import { BaseModal } from './base';

export interface Checklist extends BaseModal {
  name: string;
  vehicle_type: string;
  questions?: Question[];
}

export interface Question extends BaseModal {
  question: string;
  order: number;
  checklist_id?: number;
  type: QuestionTypes;
  additional_info?: string;
}

export const QuestionTypes = Object.freeze({
  TrueFalse: 0 as 0,
  Status: 1 as 1,
  Image: 2 as 2
});
export type QuestionTypes = Enum<typeof QuestionTypes>;

const questionTypesName = ['Có/Không', 'Trạng thái', 'Hình ảnh'];

export function getQuestionTypeName(type: QuestionTypes) {
  return questionTypesName[type];
}
