import { CircularProgress } from '@material-ui/core';
import React from 'react';
import Button from '../../components/Button';
import Input from '../../components/InputLogin';
import { Form, Logo, LogoRoot } from './styled';

type P = {
  onSubmit?: (username: string, password: string) => void;
  isLoading?: boolean;
};

type S = {
  usernameError?: string;
  passwordError?: string;
  error?: string;
};

const defaultState = Object.freeze<S>({});

class LoginForm extends React.Component<P, S> {
  state = defaultState;

  handleFormSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    const username: string | undefined = e.currentTarget.username?.value;
    const password: string | undefined = e.currentTarget.password?.value;
    const errors: S = {};
    if (!username) {
      errors.usernameError = 'Username is required';
    }
    if (!password) {
      errors.passwordError = 'Password is required';
    }
    if (Object.keys(errors).length) {
      this.setState(errors);
      return;
    }
    this.props.onSubmit?.(username!, password!);
  };
  render() {
    return (
      <Form onSubmit={this.handleFormSubmit}>
        <LogoRoot>
          <Logo src={`${process.env.PUBLIC_URL}/trungnam.svg`} alt="trungnam" />
        </LogoRoot>
        <div className="form-row">
          <Input
            size="small"
            error={!!this.state.usernameError}
            helperText={this.state.usernameError}
            name="username"
            defaultValue="thaint.uit@gmail.com"
            placeholder="Username"
            label="Username"
            variant="outlined"
          />
        </div>
        <div className="form-row">
          <Input
            size="small"
            error={!!this.state.passwordError}
            helperText={this.state.passwordError}
            defaultValue="aa"
            type="password"
            name="password"
            placeholder="Password"
            variant="outlined"
            label="Password"
          />
        </div>
        {!!this.state.error && (
          <div className="form-row form-error">{this.state.error}</div>
        )}
        <div className="form-row">
          <Button
            title="Login"
            type="submit"
            background="var(--color-primary)"
          />
        </div>
      </Form>
    );
  }
}

export default LoginForm;
