import React, { useEffect, useState, ReactElement } from 'react';
import { RouterProps } from 'react-router';
import Header from '../../layout/Header';
import {
  BtnAddUser,
  ContentSalary,
  DivNoData,
  Footer,
  Root,
  StyleContentLeft,
  StyleContentRight,
  WrapContent,
  WrapStatus,
  WrapTableContent
} from './styled';
import ScrollView from '../../components/ScrollView';
import Button from '../../components/Button';
import Loading from '../../layout/Loading';
import { toast } from 'react-toastify';
import LabelHeader from '../../layout/LabelHeader';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import TableView from '../../layout/TableView';
import PaginationPage from '../../components/Pagination';
import MenuInfo from './component/MenuInfo';
import { AlignType } from '../../utils/type';
import { TableColumnType } from '../../components/Table';
import { formatPageNumber } from '../../utils/format';
import departmentApis from '../../api/department';

type P = RouterProps;

export const Department: React.FC<P> = (p) => {
  const [valueData, setValueData] = useState<any>([]);
  const [itemSelect, setItemSelect] = useState<string>('');
  const [init, setInit] = useState<any>({
    current_page: 1,
    total_page: 1
  });

  useEffect(() => {
    getListDepartment(init.current_page);
  }, []);

  function getListDepartment(page: number) {
    departmentApis
      .getList(page)
      .then((res: any) => {
        const { data } = res;
        const newInit = {
          current_page: res.current_page,
          total_page: formatPageNumber(res.total)
        };
        setInit(newInit);
        setValueData(renderListCompany(data, res.from));
      })
      .catch((err) => {
        toast.error(err?.msg || 'Xảy ra lỗi bất ngờ');
      });
  }

  function renderListCompany(data: any, from: number) {
    let result = [];
    result = data?.map((elm: any, index: any) => {
      return {
        key: ('0' + (index + from)).slice(-2),
        id: elm.id,
        name: elm.name,
        created_at: elm.created_at
      };
    });
    return result;
  }

  const BtnAddBankList: ReactElement = (
    <BtnAddUser>
      <Button
        title="Danh sách Phòng ban"
        icon={<AddCircleIcon fontSize="small" className="iconButton" />}
        colorIcon="var(--color-primary)"
        onClick={() => {
          setItemSelect('New' + Math.random());
        }}
      />
    </BtnAddUser>
  );
  console.log('init', init);
  return (
    <Root>
      <Header disableInput={false} />
      <WrapContent>
        <ContentSalary>
          <StyleContentLeft>
            <LabelHeader title={BtnAddBankList} onFilterClick={() => {}} />
            <ScrollView>
              <WrapTableContent>
                <TableView
                  onSort={(e: any) => {}}
                  columns={columnsTable}
                  dataSource={valueData}
                  onSelectRow={(item: any) => {
                    setItemSelect(item.id + '');
                  }}
                />
                {!valueData?.length && <DivNoData>Không có dữ liệu</DivNoData>}
              </WrapTableContent>
            </ScrollView>
            {/* {this.props.loading && <Loading />} */}
          </StyleContentLeft>
          {!!itemSelect && (
            <StyleContentRight>
              <MenuInfo
                idCode={itemSelect}
                onLoad={() => {
                  getListDepartment(init.current_page);
                }}
              />
            </StyleContentRight>
          )}
        </ContentSalary>
        <Footer>
          <PaginationPage
            defaultPage={init.current_page}
            pageTotal={init.total_page}
            onChange={(e: number) => getListDepartment(e)}
          />
        </Footer>
      </WrapContent>
    </Root>
  );
};

const columnsTable: TableColumnType[] = [
  {
    key: 1,
    dataIndex: 'key',
    title: 'STT',
    align: 'center' as AlignType
  },
  {
    key: 2,
    dataIndex: 'name',
    title: 'Tên tiếng việt',
    align: 'left' as AlignType,
    isSort: true
  },
  {
    key: 3,
    dataIndex: 'created_at',
    title: 'Ngày tạo',
    align: 'left' as AlignType
  }
];
