import React, { Component } from 'react';
import { InputContent, StyleInput } from './styled';
import VisibilityIcon from '@material-ui/icons/Visibility';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';

interface InputProps {
  title?: string | number;
  name?: string | undefined;
  value?: string | number;
  valueInput?: (e: string | null) => void;
  isDisable?: boolean;
  type?: string;
  maxText?: number;
  step?: number;
  min?: number;
  max?: number;
}

export default class Input extends Component<InputProps> {
  state = {
    vInput: '',
    isShowPassword: true
  };

  componentDidMount() {
    this.setState({ vInput: this.props.value });
  }

  static getDerivedStateFromProps(nextProps: any, prevState: any) {
    if (nextProps.value !== prevState.value) {
      return { value: nextProps.value };
    }
    return null;
  }

  componentDidUpdate(prevProps: any, prevState: any) {
    if (prevProps.value !== this.props.value) {
      this.setState({ vInput: this.props.value, isShowPassword: false });
    }
  }

  handleInputChange = (e: string = '') => {
    if (this.props.min && this.props.max) {
      if (
        (this.props.type === 'number' && Number(e) < this.props.min) ||
        (this.props.type === 'number' && Number(e) > this.props.max)
      ) {
        return;
      }
    }

    this.setState({ vInput: e }, () => {
      this.props.valueInput?.(this.state.vInput);
    });
  };

  render() {
    let { vInput, isShowPassword } = this.state;
    let { title, name, type, isDisable, step, min, max } = this.props;
    return (
      <StyleInput className="styleInput">
        {title && <span className="labelTitle">{title}</span>}
        <InputContent
          name={name}
          type={
            type === 'password' && isShowPassword
              ? 'password'
              : type === 'password' && !isShowPassword
              ? 'text'
              : type
          }
          step={step || 0.1}
          min={min || 0}
          max={max}
          value={vInput}
          disabled={isDisable}
          onChange={(e) => this.handleInputChange(e.target.value)}
          onKeyDown={(evt) =>
            type === 'number' && evt.key === 'e' && evt.preventDefault()
          }
          className={type === 'password' ? 'inPutPassword' : 'inputStyle'}
        />
        {type === 'password' && (
          <>
            {!isShowPassword ? (
              <VisibilityIcon
                fontSize="small"
                className="iconInput"
                onClick={() => this.setState({ isShowPassword: true })}
              />
            ) : (
              <VisibilityOffIcon
                fontSize="small"
                className="iconInput"
                onClick={() => this.setState({ isShowPassword: false })}
              />
            )}
          </>
        )}
      </StyleInput>
    );
  }
}

