import React, { useState, CSSProperties, useEffect } from 'react';
import Select, { ValueType, StylesConfig } from 'react-select';
import { StyleSelectBox } from './styled';

export type OptionType = {
  value: string;
  label: string;
};

interface SelectProps {
  optionDefault: OptionType[];
  valueDefault?: OptionType;
  onChangeOption: (value: OptionType) => void;
  isDisabled?: boolean;
  isClearable?: boolean;
  menuPortalTarget?: boolean;
  maxMenuHeight?: number | undefined;
  minMenuHeight?: number | undefined;
}

type S = {
  selectedOption: ValueType<OptionType, false>;
  options: OptionType[];
};

const initState = Object.freeze<S>({
  selectedOption: { value: '', label: '' },
  options: [{ value: '', label: '' }]
});

class SelectBox extends React.Component<SelectProps> {
  state = initState;

  componentDidMount() {
    this.setState({ selectedOption: this.props.valueDefault });
  }

  static getDerivedStateFromProps(nextProps: any, prevState: any) {
    if (
      JSON.stringify(nextProps.valueDefault) !==
      JSON.stringify(prevState.valueDefault)
    ) {
      return { valueDefault: nextProps.valueDefault };
    }
    return null;
  }

  componentDidUpdate(prevProps: any, prevState: any) {
    if (
      JSON.stringify(prevProps.valueDefault) !==
      JSON.stringify(this.props.valueDefault)
    ) {
      this.setState({ selectedOption: this.props.valueDefault });
    }
  }

  handleChange = (option: OptionType | null) => {
    this.setState({ selectedOption: option }, () => {
      this.props.onChangeOption(option || { value: '', label: '' });
    });
  };

  render() {
    let {
      valueDefault,
      isDisabled,
      optionDefault,
      isClearable,
      menuPortalTarget = true,
      maxMenuHeight = 300,
      minMenuHeight
    } = this.props;
    let { selectedOption } = this.state;
    return (
      <StyleSelectBox className="styleSelectBox">
        <Select
          defaultValue={valueDefault}
          value={selectedOption as ValueType<OptionType, false>}
          onChange={(option) => this.handleChange(option)}
          options={optionDefault}
          styles={customStyles}
          menuPortalTarget={menuPortalTarget ? document.body : null}
          isClearable={isClearable}
          isDisabled={isDisabled}
          maxMenuHeight={maxMenuHeight}
          minMenuHeight={minMenuHeight}
        />
      </StyleSelectBox>
    );
  }
}

export default SelectBox;

const customControlStyles: CSSProperties = {
  width: '100%',
  border: 'none',
  background: '#f2f2f1',
  padding: '4px',
  position: 'relative'
};

type IsMulti = false;

const customStyles: StylesConfig<OptionType, IsMulti> = {
  control: (provided, state) => {
    const { selectProps } = state;
    return {
      ...provided,
      ...customControlStyles
    };
  }
  // option: (provided, state) => {
  //   return {
  //     ...provided,
  //     ...customOptionStyles
  //   };
  // }
};
