import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
import React, { useState } from 'react';
import { Redirect, useHistory } from 'react-router';
import { loginUser } from '../../api';
import LocalStorageHelper from '../../helpers/local-storage';
import LoginForm from './LoginForm';
import { Root } from './styled';

type P = {};

interface LoginParams {
  username: string;
  password: string;
}

function Alert(props: any) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

export const LoginPage: React.FC<P> = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [open, setOpen] = useState(false);
  const [alert, setAlert] = useState<{
    msg?: string;
    type?: 'error' | 'info' | 'success';
  }>({});
  // const dispatch = useDispatch();

  const User = JSON.parse(LocalStorageHelper.user || '{}');
  const token = LocalStorageHelper.token;
  if (User.id && token) {
    return <Redirect to="/" />;
  }

  const history = useHistory<{ prevLocation: Location }>();

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Root>
      <LoginForm
        isLoading={isLoading}
        onSubmit={(username, password) => {
          setIsLoading(true);
          //  handle login
          loginUser(username, password)
            .then((data: any) => {
              LocalStorageHelper.token = data.token;
              LocalStorageHelper.user = JSON.stringify(data.user);
              console.log('here');
              history.push(history.location.state?.prevLocation || '/');
            })
            .catch((error) => {
              setAlert({ msg: error?.message, type: 'error' });
            });
        }}
      />

      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity={alert?.type}>
          {alert?.msg}
        </Alert>
      </Snackbar>
    </Root>
  );
};
