import styled from "styled-components";
import {Link} from 'react-router-dom';

export const StyleWrap = styled.div`
    display: block;
    width: 100%;
    box-sizing: border-box;

`

export const StyleUl = styled.ul`
    display: flex;
    justify-content: space-around;
    max-width: 500px;
    list-style-type: none;
    padding: 0;
    
`;

export const StyleLi = styled.li`
    font-size: var(--font-size-h5);
    padding:var(--space-1) var(--space-0);
    margin:0px var(--space-2);
    position: relative;
    cursor: pointer;
    

    :after{
        content: '';
        position: absolute;
        width: 0;
        height: 2px;
        bottom: 0;
        background: var(--liner-color);
        display: block;
        transition: all linear 0.3s;
    }

    :before{
        content: "";
        position: absolute;
        left: 0;
        width: 120%;
        height: 20px;
        bottom: -14px;
    }

    :hover{
        color: var(--color-primary);
        font-weight: 500;

        .link{
            color: var(--color-primary);
            font-weight: 500;
        }
        .styleUlSub{
            display: block;
        }

        :after{
            width: 100%;
        }
    }
`

export const StyleLink = styled(Link)`
    text-decoration: none;
    color: var(--test-color);
`

export const StyleUlSub = styled.ul`
    display: none;
    position: absolute;
    color: #000;
    background: #fff;
    left: 0;
    list-style-type: none;
    top: 125%;
    padding: var(--space-2) 0;
    border-radius: var(--space-2);
    box-sizing: border-box;
    z-index: 1000;
    -webkit-box-shadow: 0px 2px var(--space-3) -4px #000000; 
    box-shadow: 0px 2px var(--space-3) -4px #000000;
`
export const StyleLiSub = styled.li`
    display: flex;
    min-width:210px;
    padding: var(--space-1) var(--space-4);
    box-sizing: border-box;
    position: relative;

    .subMenuUl{
        position: absolute;
        top: 0;
        right: 0;
        left: 100%;
        min-width:210px;
        margin-left:6px ;
        overflow: hidden;
        -webkit-box-shadow: 0px 2px var(--space-3) -4px #000000; 
        box-shadow: 0px 2px var(--space-3) -4px #000000;
        .styleLiSub{
            :hover{
                .subLinkChild{
                    font-weight: 500;
                    color: var(--color-primary);
                    font-size: var(--font-size-h5);
                }
            }
        }
    }
    
    :before {
        content: "";
        position: absolute;
        right:-10px;
        top: 5px;
        width: var(--space-3);
        height: 110%;
        bottom: -14px;
    }

    :hover{
        .subLink{
            font-weight: 500;
            color: var(--color-primary);
            font-size: var(--font-size-h5);

            .iconArrow{
                color: var(--color-primary);
                font-size: var(--space-3);
            }
        }
        .subMenuUl{
            display: block;
        }
    }
`

export const StyleLinkSub = styled(Link)`
    display: flex;
    width: 100%;
    text-decoration: none;
    color: var(--test-color);
    justify-content: space-between;
    align-items: center;
    font-size:var(--font-size-h6);
    font-weight: 300;
    line-height: var(--font-size-h3);

.iconArrow{
    color:#C1C3D1;
    font-size: var(--space-2);
}
`

export const UlSubChild = styled.ul`
    position: absolute;
    display: flex;
    width: 100%;
    right: 0;
    background: red;

`