import { getDefaultNormalizer } from '@testing-library/dom';
import axios, { AxiosRequestConfig } from 'axios';
import { config as appConfig } from '../constance';
import LocalStorageHelper from '../helpers/local-storage';

const HOST = 'https://xmtb-trung-nam.vipn.net/api/';
const token = localStorage.getItem('trungnam_cms-token');

interface Res<T> {
  success: boolean;
  msg: string;
  error: any | null;
  code: number;
  data: T;
}

export interface ListData<T> {
  current_page: number;
  data: T[];
  per_page: number;
  from: number;
  to: number;
  total: number;
  last_page: number;
}

export interface ListQuery {
  [key: string]: number | string | undefined | null;
  page?: number | string | null;
  limit?: number | string | null;
  keyword?: string | null;
}

export default function request<T>(config: AxiosRequestConfig) {
  const headers: Record<string, string> = {
    Authorization: `Bearer ${LocalStorageHelper.token || ''}`,
    ...config.headers
  };
  config.headers = headers;
  return axios
    .request<Res<T>>(config)
    .then((res) => res.data)
    .then((res) => {
      if (!res.success) {
        throw res;
      }
      return res.data;
    });
}

export function uploadFile(data: FormData) {
  return request({
    url: `${appConfig.hostFileManage}/file/`,
    method: 'POST',
    data
  });
}

export function loginUser(email: string, passWord: string) {
  return request({
    url: `${HOST}login`,
    method: 'POST',
    data: {
      email,
      password: passWord
    }
  });
}

export function getProvinceList() {
  return request({
    url: `${HOST}province`,
    method: 'GET',
  });
}

export function getCompanyList(page: number) {
  return request({
    url: `${HOST}company`,
    method: 'GET',
    params: {
      page: page
    },
  });
}

export function getCompanyDetail(company: string) {
  return request({
    url: `${HOST}company/${company}`,
    method: 'GET',

  });
}

interface AddCompany {
  name: string;
  eng_name: string;
  province_id: string;
}

export function addCompany(param: AddCompany) {
  return request({
    url: `${HOST}company`,
    method: 'POST',
    params: param,
  });
}

interface UpdateCompany {
  name?: string;
  eng_name?: string;
  province_id?: string;
}

export function updateCompany(idCompany:string,param: UpdateCompany) {
  return request({
    url: `${HOST}company/${idCompany}`,
    method: 'PUT',
    params: param,
  });
}

export function deleteCompany(idCompany:string) {
  return request({
    url: `${HOST}company/${idCompany}`,
    method: 'DELETE',
  });
}