import React, { ReactChild } from 'react';
import { StyleModal, WrapModal } from './styled';

export interface ModalProps {
  visible: boolean;
}

interface Children extends ModalProps {
  children: ReactChild | undefined;
}

const Modal: React.FC<Children> = ({ visible, children }) => {
  return (
    <StyleModal visible={visible} className="styleModal">
      <WrapModal visible={visible}>{children}</WrapModal>
    </StyleModal>
  );
};
export default Modal;