import React, { useEffect, useState } from 'react';
import { Route, RouterProps, Switch } from 'react-router-dom';
import Header from '../../layout/Header';
import { ListChecklist } from './pages/ListCheckList';
import { QuestionList } from './pages/QuestionsList';
import { Root } from './styled';

type P = RouterProps;

export const ChecklistPage: React.FC<P> = (p) => {
  const [valueData, setValueData] = useState<any>();
  const [init, setInit] = useState<any>({
    filter: '',
    skip: 0,
    take: 10
  });

  return (
    <Root>
      <Header disableInput={false} />
      <Switch>
        <Route path="/checklist" exact={true}>
          <ListChecklist />
        </Route>
        <Route path="/checklist/:id" exact={true}>
          <QuestionList />
        </Route>
      </Switch>
    </Root>
  );
};
