import { OptionType } from './option';

export function renderOptionSelect(param: any) {
  let result: OptionType[] = [{ value: '', label: 'Select' }];
  if (!param?.length) return result;
  result = param.map((elm: any) => {
    return { value: elm.id, label: elm?.user?.name || elm?.name  || elm?.code};
  });
  return result;
}

export function returnValueDefaultSelect(
  option: OptionType[],
  param: string | number | undefined = ''
) {
  if (!param) {
    return { value: '', label: 'Select...' };
  }

  let result = option.filter((elm) => elm.value === param);

  return result[0];
}

export function renderOptionSelectMT(param: any) {
  let result = [];
  if (!param?.length) {
    return (result = []);
  }
  result = param?.map((elm: any) => {
    return { value: elm.id, label: elm.name, color: '#ae91fd' };
  });
  return result;
}
