import React from 'react';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import InputSearch from '../../components/InputSearch';
import Menu from '../../components/Menu';
import { useHistory } from 'react-router';

import {
  Logout,
  RootHeader,
  StyleContent,
  StyleIcon,
  StyleImage,
  StyleInput,
  StyleLinkLogo,
  StyleLogo,
  StyleMenu,
  StyleSearchIcon,
  WrapImage,
  WrapLogout
} from './styled';
import { toast } from 'react-toastify';
import LocalStorageHelper from '../../helpers/local-storage';

interface Props {
  onChangInputSearch?: (e: any) => void;
  disableInput?: boolean;
}

const Header: React.FC<Props> = ({
  onChangInputSearch,
  disableInput = true
}) => {
  const history = useHistory<{ prevLocation: Location }>();
  const User = JSON.parse(localStorage.getItem('ibe_cms-user-id') || '{}');

  function handleLogout() {
    toast.success('Logout success!');
    localStorage.removeItem('trungnam_cms-user');
    localStorage.removeItem('trungnam_cms-token');
    history.push('/login');
  }
  return (
    <RootHeader>
      <StyleLinkLogo to="/">
        <StyleLogo src={`${process.env.PUBLIC_URL}/trungnam.svg`} alt="logo" />
      </StyleLinkLogo>
      <StyleContent>
        <StyleMenu>
          <Menu />
        </StyleMenu>

        <StyleSearchIcon>
          {disableInput && (
            <StyleInput>
              <InputSearch
                onChangeValue={(e) => {
                  onChangInputSearch?.(e);
                }}
              />
            </StyleInput>
          )}

          <StyleIcon>
            {User?.name || 'Guest'}
            <WrapImage>
              {User?.image ? (
                <StyleImage src={User?.image} alt="user" />
              ) : (
                <AccountCircleIcon className="iconUser" />
              )}
              <WrapLogout className="logout">
                <Logout onClick={() => handleLogout()}>Logout</Logout>
              </WrapLogout>
            </WrapImage>
          </StyleIcon>
        </StyleSearchIcon>
      </StyleContent>
    </RootHeader>
  );
};
export default Header;
