import { AxiosRequestConfig } from 'axios';
import api from './';

function request<T>(config: AxiosRequestConfig) {
  const BASE_URL = 'https://xmtb-trung-nam.vipn.net/api/dept';
  config.baseURL = BASE_URL;
  return api<T>(config);
}

const departmentApis = {
  getList: (page: number) => {
    return request({
      method: 'GET',
      url: `/`,
      params: {
        page: page
      }
    });
  },

  getDetail: (id: number | string) => {
    return request({
      method: 'GET',
      url: `/${id}`
    });
  },
  create: (body: any) => {
    return request({
      method: 'POST',
      url: `/`,
      data: body
    });
  },
  update: (id: string | number, body: any) => {
    return request({
      method: 'PUT',
      url: `https://xmtb-trung-nam.vipn.net/api/dept/${id}`,
      data: body
    });
  },
  delete: (id: string | number) => {
    return request({
      method: 'DELETE',
      url: `https://xmtb-trung-nam.vipn.net/api/dept/${id}`,
    });
  }
};

export default departmentApis;
