// import gql from '@apollo/client';
import React from 'react';
import { useHistory } from 'react-router';
import {
  HashRouter as Router,
  // Redirect,
  Route,
  Switch
} from 'react-router-dom';
import styled from 'styled-components';
import { ChecklistPage } from './Checklist';
import { Company } from './Company';
import { Department } from './Department';

const Root = styled.div`
  display: flex;
  flex-direction: column;
  height: 100%;
`;

const MainTag = styled.main`
  flex: 1;
`;

export const MainRoute: React.FC = () => {
  const history = useHistory<{ prevLocation: Location }>();
  const User = JSON.parse(localStorage.getItem('trungnam_cms-user') || '{}');
  const token = localStorage.getItem('trungnam_cms-token');

  return (
    <Root>
      {(!User.id || !token) && history?.push('/login')}
      <MainTag>
        <Router>
          <Switch>
            <Route path="/department" component={Department} />
            <Route path="/checklist" component={ChecklistPage} />
            <Route path="/" component={Company} />
          </Switch>
        </Router>
      </MainTag>
    </Root>
  );
};
