import {
  IconButton,
  TableBody,
  TableContainer,
  TableRow,
  Tooltip
} from '@material-ui/core';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import React, { useState } from 'react';
import { Table as StyledTable, TCell, THead, TRow, WrapTable } from './styled';

export type TableColumnType = {
  key?: React.Key | null;
  dataIndex?: string;
  title: string | JSX.Element;
  render?: (data: any, row: any, idx: number) => string | JSX.Element;
  align?: 'left' | 'right' | 'center';
  isSort?: boolean;
};

export type TableProps = {
  columns: TableColumnType[];
  dataSource?: any[];
  rowKey?: (row: any, index: number) => React.Key | null | undefined;
  size?: 'medium' | 'small';
  onRowClick?: (row: any, index: number) => void;
  onHeaderClick?: (value: any) => void;
  rowClassName?: (row: any, idx: number) => string | undefined;
  stickyHeader?: boolean;
};

function extractDataKey(
  dataIndex: string | undefined,
  row: any
): string | number | undefined {
  if (typeof dataIndex === 'string') {
    return row[dataIndex];
  }
  if (Array.isArray(dataIndex)) {
    const accessKeyRecurse = (obj: any, keys: string[]): any => {
      const [key, ...rest] = keys;
      if (rest.length && obj[key]) {
        return accessKeyRecurse(obj[key], rest);
      }
      return obj[key];
    };
    return accessKeyRecurse(row, dataIndex);
  }
  return row;
}

const Table: React.FC<TableProps> = ({
  columns,
  dataSource,
  rowKey,
  size = 'medium',
  onRowClick,
  onHeaderClick,
  rowClassName,
  stickyHeader = false
}) => {
  const [sortBy, setSortBy] = useState<any>();
  const [onSort, setOnSort] = useState<boolean>(false);
  return (
    <WrapTable
      style={{
        flex: 1
      }}>
      <TableContainer
        style={{
          position: stickyHeader ? 'absolute' : 'relative',
          width: '100%',
          maxHeight: '100%'
        }}>
        <StyledTable stickyHeader={stickyHeader}>
          <THead>
            <TableRow>
              {columns?.map((col, idx) => {
                return (
                  <TCell
                    onClick={
                      col.isSort
                        ? () => {
                            onHeaderClick?.({
                              sort_by: col.dataIndex,
                              sort: onSort
                            });
                            setSortBy(col.dataIndex);
                            setOnSort(!onSort);
                          }
                        : undefined
                    }
                    className={
                      idx === 0
                        ? 'first-cell'
                        : idx === columns.length - 1
                        ? 'last-cell'
                        : ''
                    }
                    size={size}
                    key={col.key}
                    align={col.align}>
                    {col.title}
                    {col.isSort && (
                      <IconButton>
                        {onSort && col.dataIndex === sortBy ? (
                          <ArrowDropUpIcon
                            fontSize="inherit"
                            style={{
                              color: col.dataIndex === sortBy ? '#fff' : ''
                            }}
                          />
                        ) : (
                          <ArrowDropDownIcon
                            fontSize="inherit"
                            style={{
                              color: col.dataIndex === sortBy ? '#fff' : ''
                            }}
                          />
                        )}
                      </IconButton>
                    )}
                  </TCell>
                );
              })}
            </TableRow>
          </THead>
          <TableBody>
            {dataSource?.map?.((row, idx) => (
              <TRow
                onClick={() => {
                  onRowClick?.(row, idx);
                }}
                key={rowKey?.(row, idx)}>
                {columns?.map((col, cIdx) => {
                  if (col.dataIndex === 'created_at') {
                    console.log(row);
                  }
                  const colData = extractDataKey(col.dataIndex, row);
                  const className =
                    cIdx === 0
                      ? ['first-cell ']
                      : cIdx === columns.length - 1
                      ? ['last-cell ']
                      : [];
                  if (rowClassName) {
                    const classStr = rowClassName(row, idx);
                    if (classStr) {
                      className.push(classStr);
                    }
                  }

                  return (
                    <TCell
                      size={size}
                      className={className.join(' ')}
                      key={col.key}
                      align={col.align}>
                      {col.render?.(colData, row, idx) || colData?.toString()}
                    </TCell>
                  );
                })}
              </TRow>
            ))}
          </TableBody>
        </StyledTable>
      </TableContainer>
    </WrapTable>
  );
};

export default Table;
