type Status = 'Active' | 'Inactive';

interface MenuInfoProps {
  idCode: string;
  onLoad: () => void;
}

let valueChangeInput: any = {};
const MenuInfo: React.FC<MenuInfoProps> = ({ idCode, onLoad }) => {
  const [valueDetail, setValueDetail] = useState<any>();
  const [provinceList, setProvinceList] = useState<any>();
  const [isDisable, setIsDisable] = useState<boolean>(true);
  const [isOpenNoti, setIsOpenNoti] = useState<boolean>(false);

  useEffect(() => {
    setIsDisable(true);
    setValueDetail('');
    if (idCode.indexOf('New') != -1) {
      return;
    }
    getDetailDepartment(idCode);
  }, [idCode]);

  function getDetailDepartment(idCode: string) {
    departmentApis
      .getDetail(idCode)
      .then((res: any) => {
        setValueDetail(res);
      })
      .catch((err) => {
        toast.error(err?.message || 'Xảy ra lỗi bất ngờ');
      });
  }

  function handleAddDepartment() {
    if (!valueChangeInput.name) {
      toast.warning('Vui lòng nhập đủ thông tin!');
      return;
    }
    departmentApis
      .create(valueChangeInput)
      .then((res: any) => {
        toast.success('Thêm Thành Công!');
        console.log('red=======', res);
        setValueDetail(res);
        onLoad();
      })
      .catch((err) => {
        toast.error(err?.msg || 'Xảy ra lỗi bất ngờ');
      });
  }

  function handleUpdateDepartment() {
    console.log('handleUpdateDepartment', valueChangeInput);
    if (valueChangeInput.name === '') {
      toast.warning('Vui lòng nhập đủ thông tin!');
      return;
    }
    const valueUpdate = {
      name: valueChangeInput.name
    };
    departmentApis
      .update(valueDetail?.id, valueUpdate)
      .then((res: any) => {
        toast.success('Update Thành Công!');
        setIsDisable(true);
        onLoad();
      })
      .catch((err) => {
        toast.error(err?.msg || 'Xảy ra lỗi bất ngờ');
      });
  }

  function handleDeleteDepartment() {
    departmentApis
      .delete(valueDetail?.id)
      .then((res: any) => {
        toast.success('Delete Thành Công!');
        setValueDetail('');
        onLoad();
      })
      .catch((err) => {
        toast.error(err?.msg || 'Xảy ra lỗi bất ngờ');
      });
  }
  console.log('valueDetail', valueDetail);
  return (
    <ContentRight
      childrenTop={
        <>
          <div className="idCode">
            Code#
            <div className="code">{valueDetail?.id || 'New_Code'}</div>
          </div>
          {valueDetail?.updated_at && (
            <span className="timeUpdate">
              Update at&nbsp;
              <span className="time">
                {valueDetail?.updated_at || valueDetail?.create_at}
              </span>
            </span>
          )}
          {/* { <Loading />} */}
          <Notification
            visible={isOpenNoti}
            title={`Xác nhận xóa: ${valueDetail?.name}`}
            onclose={() => {
              setIsOpenNoti(false);
            }}
            onConfirm={() => {
              setIsOpenNoti(false);
              handleDeleteDepartment();
            }}
          />
        </>
      }
      childrenContent={
        <>
          <>
            <Input
              title="Tên phòng ban"
              value={valueDetail?.name || ''}
              valueInput={(e: string | null) => {
                isDisable && setIsDisable(false);
                valueChangeInput.name = e;
              }}
            />
          </>
        </>
      }
      titleConfirm={!valueDetail?.name ? 'Add' : 'Update'}
      titleReject="Delete"
      disableBtnConfirm={isDisable}
      onConfirm={() => {
        !valueDetail?.name ? handleAddDepartment() : handleUpdateDepartment();
      }}
      onReject={
        !valueDetail?.name
          ? undefined
          : () => {
              setIsOpenNoti(true);
            }
      }
    />
  );
};

export default MenuInfo;

