import { Button, SvgIcon } from '@material-ui/core';
import { mdiHelpCircle, mdiPlus } from '@mdi/js';
import qs from 'query-string';
import React, { ChangeEventHandler, useEffect, useState } from 'react';
import { RouterProps, useHistory } from 'react-router';
import { toast } from 'react-toastify';
import { ListData } from '../../../../api';
import checklistApis from '../../../../api/checklist';
import PaginationPage from '../../../../components/Pagination';
import Table from '../../../../components/Table';
import { useQuery } from '../../../../hook/use-query';
import { Checklist } from '../../../../models/Checklist';
import { formatDateTime } from '../../../../utils/format';
import { DetailForm } from './DetailForm';
import { Content, Input, Root, Title } from './styled';

type P = {};

let searching: number | undefined;
export const ListChecklist: React.FC<P> = (p) => {
  const query = useQuery<{
    page?: number | string;
    create?: string | boolean;
    keyword?: string;
    id?: string | number;
  }>();
  const history = useHistory();
  const [res, setRes] = useState<ListData<Checklist>>();
  const isCreate = query.create?.toString() === 'true';
  const [search, setSearch] = useState(query.keyword || '');

  const checklist =
    res?.data && query.id
      ? res?.data.find((e) => +query.id! === e.id)
      : undefined;

  useEffect(() => {
    checklistApis
      .getList({
        page: query.page ?? 1,
        keyword: query.keyword,
        limit: 10
      })
      .then((r) => {
        setRes(r);
      })
      .catch((e) => {
        toast.error(e.msg || e.message || 'Lỗi');
      });
  }, [query.page, query.keyword]);

  function handleDetailUpdated(data: Checklist) {
    const idx = res?.data.findIndex((e) => e.id === data.id);
    if (idx !== undefined) {
      res!.data[idx] = data;
      setRes({ ...res! });
    }
  }

  const handleSearch: ChangeEventHandler<HTMLInputElement> = (e) => {
    const v = e.currentTarget.value;
    setSearch(v);
    if (searching) {
      window.clearTimeout(searching);
    }

    searching = window.setTimeout(() => {
      searching = undefined;
      history.push({
        pathname: history.location.pathname,
        search: qs.stringify({
          keyword: v
        })
      });
    }, 200);
  };

  const handleDelete = (row: Checklist) => {
    checklistApis
      .delete(row.id)
      .then(() => {
        toast.success('Xóa thành công');
        return checklistApis
          .getList({
            page: query.page ?? 1,
            keyword: query.keyword,
            limit: 10
          })
          .then((r) => {
            setRes(r);
          });
      })
      .catch((e) => {
        toast.error(e.msg || e.message || 'Lỗi');
      });
  };

  return (
    <Root>
      <Content>
        <Title>
          Bộ câu hỏi
          <Button
            color="primary"
            onClick={() => {
              history.push({
                pathname: history.location.pathname,
                search: qs.stringify({
                  ...query,
                  id: undefined,
                  create: true
                })
              });
            }}>
            <SvgIcon>
              <path d={mdiPlus} />
            </SvgIcon>
            Thêm bộ câu hỏi
          </Button>
          <div style={{ fontSize: 14 }}>
            <Input
              placeholder="Tìm kiếm"
              value={search}
              onChange={handleSearch}
            />
          </div>
        </Title>
        <Table
          rowKey={(r: Checklist) => r.id}
          stickyHeader={true}
          dataSource={res?.data}
          rowClassName={(row) =>
            row.id === checklist?.id ? 'selected' : undefined
          }
          onRowClick={(row) => {
            history.push({
              pathname: history.location.pathname,
              search: qs.stringify({
                ...query,
                id: row.id,
                create: undefined
              })
            });
          }}
          columns={[
            {
              key: 'id',
              title: 'ID',
              dataIndex: 'id'
            },
            {
              key: 'name',
              title: 'Loại thiết bị',
              dataIndex: 'name'
            },
            {
              key: 'created_at',
              title: 'Ngày tạo',
              dataIndex: 'created_at',
              render: (v: string) => formatDateTime(new Date(v))
            },
            {
              key: 'updated_at',
              title: 'Cập nhật',
              dataIndex: 'updated_at',
              render: (v: string) => formatDateTime(new Date(v))
            },
            {
              key: 'action',
              title: 'Hành động',
              render: (row: Checklist) => (
                <div>
                  <Button
                    style={{ color: 'white', padding: 4 }}
                    onClick={(e) => {
                      e.stopPropagation();
                      handleDelete(row);
                    }}
                    color="secondary"
                    variant="contained">
                    Xóa
                  </Button>
                </div>
              )
            }
          ]}
        />
        <div>
          <PaginationPage
            onChange={(page) => {
              history.push({
                pathname: history.location.pathname,
                search: qs.stringify({
                  ...query,
                  page
                })
              });
            }}
            defaultPage={res?.current_page || 0}
            pageTotal={res?.last_page || 0}
          />
        </div>
      </Content>
      {(!!checklist || isCreate) && (
        <DetailForm
          isCreate={isCreate}
          checklist={checklist}
          onUpdated={handleDetailUpdated}
        />
      )}
    </Root>
  );
};
