import { AxiosRequestConfig } from 'axios';
import { Checklist, Question } from '../models/Checklist';
import api, { ListData, ListQuery } from './';

function request<T>(config: AxiosRequestConfig) {
  const BASE_URL = 'https://xmtb-trung-nam.vipn.net/api/checklist';
  config.baseURL = BASE_URL;
  return api<T>(config);
}

const checklistApis = {
  getList: (options?: ListQuery) => {
    return request<ListData<Checklist>>({
      method: 'GET',
      url: `/`,
      params: options
    });
  },
  getDetail: (id: number | string) => {
    return request<Checklist>({
      method: 'GET',
      url: `/${id}`
    });
  },
  create: (body: any) => {
    return request<Checklist>({
      method: 'POST',
      url: `/`,
      data: body
    });
  },
  update: (id: number | string, body: any) => {
    return request<Checklist>({
      method: 'PUT',
      url: `/${id}`,
      data: body
    });
  },
  delete: (id: number | string) => {
    return request<Checklist>({
      method: 'DELETE',
      url: `/${id}`
    });
  },
  createQuestion: (id: string | number, questions: any[]) => {
    return request({
      method: 'POST',
      url: `/${id}/question`,
      data: {
        questions
      }
    });
  },
  updateQuestion: (id: string | number, questions: any[]) => {
    return request({
      method: 'PUT',
      url: `/${id}/question`,
      data: {
        questions,
        date: new Date()
      }
    });
  },
  deleteQuestion: (id: string | number) => {
    return request({
      method: 'DELETE',
      url: `/${id}/question`
    });
  }
};

export default checklistApis;
